























































































































































































































































































































import Vue from 'vue';
import {imageModule, UserImage} from '@/store/modules/images';
import { peopleModule } from '@/store/modules/people';
import { acModule } from '@/store/modules/acs';
import { PersonData } from '@/interfaces/ipeople';
import Person from '@/data/person';
import iconConfig from '@/store/misc/icons';

export default Vue.extend({
  name: 'Settings',
  data() {
    return {
      loadingSave: false as boolean,
      loadingDelete: false as boolean,
      saveMessage: '' as string,
      deleteMessage: '' as string,
      deleteDialog: false as boolean,
      lastPerson: undefined as Person | undefined,
      selectedImage: undefined as UserImage | undefined,
      selectedName: '' as string,
      selectedColor: '' as string,
      showImages: false as boolean,
      showColors: false as boolean,
      editName: false as boolean,
      colors: [
        'red', 'pink', 'purple', 'deep-purple', 'indigo', 'blue', 'light-blue', 'cyan', 'teal', 'green', 'light-green', 'lime', 'yellow', 'amber', 'orange', 'deep-orange', 'brown', 'blue-grey', 'grey'
      ],
      nameRules: [
        (v: string) => !!v || 'Name is required',
        (v: string) => (v && v.length <= 20) || 'Name must be less than 20 characters',
      ],
      iconConfig
    }
  },
  methods: {
    selectImage(img: UserImage) {
      this.showImages = false;
      this.selectedImage = img;
    },
    selectColor(color: string) {
      this.showColors = false;
      this.selectedColor = color;
    },
    confirmForm(e: Event): void {
      e.preventDefault();
      this.editName = false;
    },
    deleteData(): void {
      this.loadingDelete = true;
      acModule.deleteAllACs()
      .then((result) => {
        if (!result) {
          this.loadingDelete = false;
          this.deleteDialog = false;
          this.deleteMessage = 'Something went wrong when deleting the Appreciation Cards.'
          return;
        }
        peopleModule.deleteAllPeople()
        .then((result) => {
          if (!result) {
            this.loadingDelete = false;
            this.deleteDialog = false;
            this.deleteMessage = 'Something went wrong when deleting the contacts.'
            return;
          }
        
          this.loadingDelete = false;
          this.deleteDialog = false;
          this.deleteMessage = 'Cleared all data.'
          window.location.reload();
        })
      })
    },
    saveData(): void {
      this.loadingSave = true;
      this.showColors = false;
      this.showImages = false;
      this.editName = false;
      const sp = peopleModule.selfPerson;
      if (!sp) return;
      const pd: PersonData = {
        name: this.selectedName,
        email: sp.email,
        deleted: sp.deleted,
        color: this.selectedColor,
        isSelf: true
      }
      if (this.selectedImage) pd.image = this.selectedImage.id;
      peopleModule.updatePerson(pd)
        .then((person: Person) => {
          this.loadingSave = false;
          if (person) {
            this.saveMessage = 'Details updated succesfully!';
            this.lastPerson = person;
            this.setVariables(person);
          } else {
            this.saveMessage = 'Something went wrong';
          }
          setTimeout(() => this.saveMessage = '', 4000);
        })
    },
    setVariables(person: Person) {
      this.selectedName = person.name;
      this.selectedColor = person.color;
      this.selectedImage = person.image;
    },
    toInstall(): void {
      window.localStorage.removeItem('noInstall');
      this.$router.replace({ name: 'Install' });
    }
  },
  computed: {
    selectedImageSource(): string {
      return this.selectedImage ? require(`@/assets/${this.selectedImage.path}`) : '';
    },
    images(): Array<UserImage> {
      return imageModule.images;
    },
    initials(): string {
      return this.selectedName.substr(0, 1).toUpperCase();
    },
    ownEmail(): string {
      return this.lastPerson ? this.lastPerson.email : '';
    },
    isInBrowser(): boolean {
      return !(matchMedia('(display-mode: standalone)').matches);
    },
    disableSave(): boolean {
      return (
        this.lastPerson !== undefined
        && this.selectedImage === this.lastPerson.image
        && this.selectedColor === this.lastPerson.color
        && this.selectedName === this.lastPerson.name
      );
    }
  },
  mounted: function() {
    this.lastPerson = peopleModule.selfPerson;
    if (!this.lastPerson) return;
    this.setVariables(this.lastPerson);
  }
});
